import { Search, Toast } from "@taroify/core"
import { View } from "@tarojs/components"
import * as React from "react"
import { useState } from "react"
import Block from "../../../components/block"
import Page from "../../../components/page"
import "./index.scss"

function BasicSearch() {
  const [value, setValue] = useState("")
  return (
    <Search
      value={value}
      placeholder=""
      onChange={(e) => setValue(e.detail.value)}
    />
  )
}

function SearchWithEvents() {
  const [value, setValue] = useState("")
  const [open, setOpen] = useState(false)

  return (
    <>
      <Toast open={open} onClose={() => setOpen(false)}>
        
      </Toast>
      <Search
        value={value}
        placeholder=""
        action
        onChange={(e) => setValue(e.detail.value)}
        onCancel={() => setOpen(true)}
      />
    </>
  )
}

function InputCenterSearch() {
  const [value, setValue] = useState("")
  return (
    <Search
      value={value}
      placeholder=""
      inputAlign="center"
      onChange={(e) => setValue(e.detail.value)}
    />
  )
}

function DisabledSearch() {
  return <Search disabled placeholder="" />
}

function BackgroundSearch() {
  return <Search className="background" shape="round" placeholder="" />
}

function CustomSearch() {
  const [value, setValue] = useState("")
  const [open, setOpen] = useState(false)
  return (
    <>
      <Toast open={open} onClose={() => setOpen(false)}>
        
      </Toast>
      <Search
        value={value}
        label=""
        placeholder=""
        action={<View onClick={() => setOpen(true)}></View>}
        onChange={(e) => setValue(e.detail.value)}
      />
    </>
  )
}

export default function SearchDemo() {
  return (
    <Page title="Search " className="search-demo">
      <Block title="">
        <BasicSearch />
      </Block>
      <Block title="">
        <SearchWithEvents />
      </Block>
      <Block title="">
        <InputCenterSearch />
      </Block>
      <Block title="">
        <DisabledSearch />
      </Block>
      <Block title="">
        <BackgroundSearch />
      </Block>
      <Block title="">
        <CustomSearch />
      </Block>
    </Page>
  )
}
